.catalog-goods {
  width: 100%;
  border-top: 1px solid $color-border;
}

.catalog-goods__wrapper {
  text-align: center;
}

.catalog-goods__title {
  @include section-title;
  padding: 0;
  margin: 0 20px 0 0;
  padding-top: 14px;
  padding-bottom: 29px;
}

.catalog-goods__list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.catalog-goods__all-goods {
  display: block;
  width: 100%;
  border-bottom: 1px solid $color-border;
}

@media (min-width: $tablet-width) {
  .catalog-goods {
    border-top: none;
  }

  .catalog-goods__wrapper {
    width: $tablet-width;
    margin-left: auto;
    margin-right: auto;
  }

  .catalog-goods__title {
    @include section-title($color-title, 60px, 60px);
    background-color: transparent;
    text-align: left;
    padding-top: 46px;
    padding-bottom: 84px;
    padding-left: 30px;
  }

  .catalog-goods__list {
    display: flex;
    flex-wrap: wrap;
  }

  .catalog-goods__item {
    width: 50%;
  }

  .catalog-goods__all-goods {
    display: flex;
    flex-direction: column;
    width: 50%;
    margin-top: auto;
    margin-bottom: auto;
  }
}

@media (min-width: $desktop-width) {
  .catalog-goods__wrapper {
    width: $desktop-width;
  }

  .catalog-goods__item {
    width: 25%;
  }

  .catalog-goods__title {
    padding-top: 46px;
    padding-left: 41px;
  }

  .catalog-goods__all-goods {
    width: 25%;
  }

  .company-good__program {
    max-width: 1300px;
    margin-left: auto;
    margin-right: auto;
    padding: 0;
  }
}
